import { Component, Input, Renderer } from '@angular/core';

@Component({
    selector: 'error-message',
    template: `
    <table id='{{id}}' *ngIf = 'control' class='error-tab' cellspacing="0" cellpadding="0" border="0">
        <tr>
            <td>
                <div class='tran' [ngClass]="{'bot-tran': position === 'bottom'}"></div>
            </td>
            <td>
                <div class='td-icon'>
                <div class='error-icon'></div>
                </div>
            </td>
            <td>
                <div class='td-text'>
                    <div *ngIf='text !== null '>{{text}}</div>
                    <div>
                        <ng-content></ng-content>
                    </div>
                </div>
            </td>
        </tr>
    </table>
    `,
    styles: [`
    :host{
        display: inline-block;
        white-space: nowrap;
        width: 100%;
        position: initial;
        padding: 0;
        height: 32px;
        margin-top: 8px;
    }
    @media (min-width: 768px){
        :host {
            width: 100%;
            white-space: nowrap;
            display: inline-block;
            position: absolute;
            padding: 0;
            height:32px; 
            margin-top: 0px;
        }
    }
    :host .error-tab {
        height: 100%;
        width: 100%;
        display: inline-block;
        border-radius: 3px; }
        :host .error-tab .tran {
            width: 10px;
            height: 10px;
            background: #feeed4;
            left: 25px;
            top: -16px;
            transform: rotate(135deg);
            -ms-transform: rotate(135deg);
            -moz-transform: rotate(135deg);
            -webkit-transform: rotate(135deg);
            -o-transform: rotate(135deg);
            border: 1px solid #d09842;
            border-right: none;
            border-top: none;
            position: relative;
         }
        
        @media (min-width: 768px){
            :host .error-tab .tran {
                width: 10px;
                height: 10px;
                background: #feeed4;
                transform: rotate(45deg);
                -ms-transform: rotate(45deg);
                /* IE 9 */
                -moz-transform: rotate(45deg);
                /* Firefox */
                -webkit-transform: rotate(45deg);
                /* Safari 和 Chrome */
                -o-transform: rotate(45deg);
                /* Opera */
                border: 1px solid #d09842;
                border-right: none;
                border-top: none;
                position: relative;
                left: 5px;
                top: 0px;
            }
        }

        :host .error-tab tr {
            height:100%;
        }

        :host .error-tab tr td {
            height:100%;
        }

        :host .error-tab .bot-tran {
        left: 25px;
        top: -16px;
        transform: rotate(135deg);
        -ms-transform: rotate(135deg);
        /* IE 9 */
        -moz-transform: rotate(135deg);
        /* Firefox */
        -webkit-transform: rotate(135deg);
        /* Safari 和 Chrome */
        -o-transform: rotate(135deg);
        /* Opera */ }
        :host .error-tab .td-icon, :host .error-tab .td-text {
        height: 100%;
        border: 1px solid #d09842;
        background: #feeed4;
        white-space: nowrap;
        padding: 0 5px; }
        :host .error-tab .td-icon {
        border-right: none;
        border-bottom-left-radius: 3px;
        border-top-left-radius: 3px; }
        :host .error-tab .td-text {
        border-top-right-radius: 3px;
        border-bottom-right-radius: 3px;
        white-space: nowrap;
        border-left: none;
        line-height: 32px; }
        :host .error-tab .error-icon {
        width: 24px;
        height: 26px;
        background-image: url(data:image/png;base64,);
        background-position-x: 70px;
        background-position-y: -5px;
        padding: 0;
        margin-top: 4px; }
    `]
})
export class PutErrorComponent {

    @Input() control: any;
    @Input() text?: string;
    @Input() position?: string;


    constructor(private renderer: Renderer) {
    }
}